.footer {
  background: linear-gradient(180deg, #ffeef5 0%, #f7d4e3 100%);
  color: var(--color-charcoal);
  padding-block: 2.5rem 2rem;
  margin-top: auto;
}

.inner {
  display: grid;
  gap: 1.5rem;
  align-items: start;
}

.signature {
  font-family: var(--font-display);
  letter-spacing: 0.08em;
  text-transform: uppercase;
  margin: 0;
  font-size: 0.95rem;
}

.smallPrint {
  margin: 0.5rem 0 0;
  font-size: 0.85rem;
  color: rgba(92, 31, 54, 0.65);
}

.footerNav {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem 1.5rem;
  font-size: 0.85rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.footerNav a {
  color: rgba(92, 31, 54, 0.75);
}

.footerNav a:hover {
  color: var(--color-charcoal);
}

.meta {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  font-size: 0.8rem;
  color: rgba(92, 31, 54, 0.6);
}

.meta a {
  color: rgba(92, 31, 54, 0.85);
}

@media (min-width: 768px) {
  .inner {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    align-items: center;
  }

  .footerNav {
    justify-content: center;
  }

  .meta {
    justify-content: flex-end;
    text-align: right;
  }
}
